.App {
  text-align: center;
}

img {
  width: 100%;
}

.container1 {
  position: relative;
  display: flex;
  flex-wrap: wrap;
}

.item {
  flex: 1;
  flex-basis: 120px;
}

.text-block {
  position: absolute;
  color: white;
  z-index: 999;
  margin: 0 auto;
  left: 0;
  right: 0;
  top: 5%; /*Adjust this value to move the positioned div up and down */
  /* width: 60%; Set the width of the positioned div */
}

.aboutme {
  background-color: #003B00;
}

.container2 {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
}

.logo {
  width: 30%;
  margin: 0 auto;
  padding: 10px;
}

