.navbar {
  /* margin: 1rem; */
  justify-content: space-between;
  background-color: #1B2326;
  color: white;
}

.overlay {
  display: none;
}

.logo:hover .overlay {
  display: inline-block;
}






h1, h2, h3, h4, h5, h6 {
	margin: 0;
	padding: 0;
	border: 0;
}











@media only screen and (min-width: 600px) {
  .navbar {
    display: flex;
    /* margin: 1rem; */
    justify-content: space-between;
    background-color: #1B2326;
    color: white;
  }
  .portfolio h2 {
    color: white;
    padding: 0 1rem;
  }
  .nav{
    display: inline-flex;
    align-items: center;
  }
  .nav h4{
    color: white;
    padding: 0 2rem;
  }
}